import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/pages/index.vue'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Hello',
        component: Hello,
        children: [{
            path: '/',
            name: 'Hello',
            component: resolve => require(['../components/treeTable.vue'],resolve),
        },{
            path: 'crossres',
            name: 'croseeRes',
            component: resolve => require(['../components/CrossReferences.vue'],resolve),
        }]
    }]
})



